import 'focus-visible';
import React, { forwardRef } from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { LinkProps } from 'core/types/link';
import {
  useHighlightColor,
  useTextDecoration,
  useFontIndex,
  useFontColor
} from 'core/shared/typographyHooks';
import useFont from 'core/shared/typographyHooks/useFont';
import { LINK_CLASS_NAME } from 'core/shared/linkHOC/constants';

import styles from './styles.scss';

const cd = cssDecorator(styles);

const Link: React.FC<LinkProps> = forwardRef(({
  children,
  href,
  target = '_blank',
  rel = 'noopener noreferrer',
  size = 'default',
  color = 'tass',
  bold,
  italic,
  highlightingColor,
  lineThrough,
  fontIndex,
  onClick,
  dataTestId
}, ref: React.Ref<HTMLAnchorElement>) => {
  const fontIndexClassName = useFontIndex(fontIndex);
  const style = useHighlightColor(highlightingColor);
  const textDecorationClassName = useTextDecoration(lineThrough);
  const colorClassName = useFontColor(color);
  const fontParams = useFont(fontIndex ? 'text' : size, bold ? 'bold' : 'regular');
  const linkClassNames = cd(
    'link',
    { italic },
    [
      fontParams,
      colorClassName,
      textDecorationClassName,
      fontIndexClassName,
      LINK_CLASS_NAME
    ]
  );

  const anchor = (
    <a
      tabIndex={ 0 }
      href={ href }
      className={ linkClassNames }
      target={ target }
      rel={ rel }
      onClick={ onClick }
      data-test-id={ dataTestId }
      ref={ ref }
      style={ style }
    >
      { children }
    </a>
  );

  return fontIndex
    ? React.createElement(fontIndex, {}, anchor)
    : anchor;
});

Link.displayName = 'Link';

export { Link };
